import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"

 class BookList extends React.Component {
  render () {
      return (
        <Layout location={this.props.location} title='book list' description="Somewhere for my books">
          <SEO title='book list' />
          <div className="blog-post-container">
            <div className="blog-post">
              <h1>{this.props.data.frontmatter.title}</h1>
              {/* <div dangerouslySetInnerHTML={{ __html: this.props.data.html }} /> */}
              </div>
          </div>
        </Layout>
      )
    }
  }

export default BookList

export const pageQuery = graphql`
  query {
    markdownRemark(
      frontmatter: {layout: {eq: "book-list"}}
    ) {
      html
      frontmatter {
        title
      }
    }
  }
`
